import { createComponentFromModel } from 'core/loadEntities';

export default function (entity) {

    const entityForces = entity.get('forces');

     if(!entityForces.parts.escapeRight && !entityForces.parts.escapeLeft){

     this.scripts.snippets['aJam']['removeInputsPerso'].call(this, entity);

    const forces = {
      name: 'forces',
      scope: 'common',
      params: {
        parts: {
          escapeLeft: {
            x: -75,
            y: 0,
            z: 0,
            rotateX: 0,
            rotateY: 0,
            duration: 500,
            easing: {
              scope: 'ease',
              name: 'ease-linear',
            },
            ending: {
              scope: 'aJam',
              name: 'persoFinishEscape',
            },
          },
        },
      },
    };
    const newForces = createComponentFromModel.call(this, forces);


    delete entityForces.parts.moveLeft;

    delete entityForces.parts.moveRight;
    entityForces.parts.escapeLeft = newForces.parts.escapeLeft;
    const spriteSheets = entity.get('sprites');

    spriteSheets.parts.main.source='perso-walk-left'

    spriteSheets.parts.main.animation.frame=0;
  }
}
